.App {
  text-align: center;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  padding: 5px 0;
  margin-top: 10px;
  justify-content: space-around;  
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header button {
  width: 20%;
  padding: 2px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 22px;
}

.App-link {
  color: #61dafb;
}
.client {
  display: flex;
  justify-content: space-between;
}
.header {
  padding: 10px 0;  
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  background-color: #ccc;
}
.client span {
  display: inline-block;
  width: 25%;
}
.client span:first-of-type {
  text-align: left;
  padding-left: 30px;
  width: 5%;
}
.data_wrap {
  width: 100%;
  overflow: auto;
}
.data {
  min-width: 500px;
}

@media screen and (max-width: 480px) {
  body {
    background-color: lightgreen;
  }
  .App-header {
    flex-direction: column;
    height: auto;
  }
  .App-header button {
    width: 98%;
    margin: 5px auto;
  }
  .client span:first-of-type {
    padding-left: 8px;
  }
  .client {
    font-size: 14px;
    align-items: center;
  }
}
